import dayjs from "dayjs";
import React, { useCallback, useEffect, useRef, useState } from "react";
import styled, { css, keyframes } from "styled-components";

import LangButton from "../../components/LangButton";

const EduPage = () => {
  const initialState = [
    "HTML",
    "STYLED",
    "JAVA",
    "REACT",
    "JSP",
    "NODE.JS",
    "Mybatis",
    "MYSQL",
    "AWS",
    "JQUERY",
    "SpringBoot",
    "JPA",
    "Jenkins",
    "SPRING",
    "RTK",
    "THUNK",
    "Typescript",
  ];

  const [firstRendering, setFirstRendering] = useState(true);
  const [langFilter, setLangFilter] = useState(initialState);
  useEffect(() => {
    if (firstRendering) {
      setFirstRendering(false);
    }
  }, [firstRendering]);
  const intro = useRef([
    {
      id: "sqisoft",
      dueDate: "2019.09 ~ ",
      startDt: "2019.09",
      endDt: "",
      companyNm: "",
      team: "",
      companyDetail:
        "        ",
      work: "(F/E)",
    },
  ]);

  const workDetail = useRef([
    {
      id: "sqisoft",
      data: [
        {
          title: "     , ",
          date: "2019.09.02~2021.12.31",
          impTxt: ` /        `,
          stack: ["SPRING", "JAVA", "JSP", "Mybatis", "MYSQL", "AWS", "JQUERY"],
          result:
            "  /    / MSA  ",
          detailWork: [
            "     ",
            "    ",
            "  ",
            "     MSA ",
            "MSA    ",
          ],
        },
        {
          title: "   (OKR)",
          date: "2020.04.01.~2020.05.10",
          impTxt: ` /      `,
          stack: ["SPRING", "JAVA", "JSP", "Mybatis", "MYSQL", "AWS", "JQUERY"],
          result: " , OKR     ",
          detailWork: [
            "OKR    ",
            "Spring boot  RESTful API ",
          ],
        },
        {
          title: " ,    ",
          date: "2021.07~2021.09",
          impTxt: `          `,
          stack: ["SPRING", "JAVA", "JSP", "Mybatis", "MYSQL", "AWS", "JQUERY"],
          result: "500    =  ",
          detailWork: [
            "      ",
            "Spring boot  RESTful API ",
          ],
        },
        {
          title: "     ",
          date: "2021.04~2021.06",
          impTxt: `    /  `,
          stack: ["REACT", "SpringBoot", "JPA", "Jenkins", "MYSQL"],
          result: "-,   ",
          detailWork: [
            "   UI ",
            "DB ",
            "Front-end ",
            "Spring boot  RESTful API ",
            " CICD   ",
          ],
        },
        {
          title: "  ",
          date: "2021.08~2021.12",
          impTxt: ` 2 MSA  Spring Cloud CICD  `,
          stack: ["REACT", "JPA", "Jenkins"],
          result: "   ",
          detailWork: [
            "MSA     ",
            "React.js, webpack  ",
            "Netflix OSS(Juul, Eureka),Spring cloud   ",
          ],
        },
        {
          title: "ELIGA   ",
          date: "2022.01~2022.03",
          impTxt: `    `,
          stack: ["REACT", "RTK", "JPA", "Jenkins"],
          result: "redux toolkit query ",
          detailWork: [
            "    ",
            "   ",
            "    ",
          ],
        },
        {
          title: "ELIGA STORE   ",
          date: "2022.05~2022.07",
          impTxt: `  `,
          stack: ["REACT", "THUNK", "NODE.JS"],
          result: "     ",
          detailWork: [
            "/ CMS    ",
            ",    -     ",
            ",  UI  ",
            "POS    ",
          ],
        },
        {
          title: " ETRI  ",
          date: "2022.07~2022.09",
          impTxt: `   ,      `,
          stack: ["REACT", "Typescript", "NODE.JS", "Jenkins"],
          result: " , Typescript  ",
          detailWork: [
            ", /   ",
            "Redux Thunk  ",
          ],
        },
        {
          title: "B2C  - /   ",
          date: "2022.10.01~2022.10.30 ()",
          impTxt: `    `,
          stack: ["REACT", "Typescript", "NODE.JS", "Jenkins"],
          result: "/    ",
          detailWork: [
            ", /   ",
            "Redux Thunk  ",
          ],
        },
      ],
    },
  ]);

  const handleClickFilter = useCallback(
    (item) => {
      const findIndex = langFilter.findIndex((lang) => lang === item);
      if (findIndex > -1) {
        setLangFilter(langFilter.filter((lang) => lang !== item));
      } else {
        setLangFilter(langFilter.concat(item));
      }
    },
    [langFilter]
  );

  return (
    <FlexTable width={"100%"} firstRendering={firstRendering}>
      <Layout>
        <table>
          <thead>
            <tr>
              <th> </th>
              <th></th>
              <th></th>
              <th> </th>
              <th></th>
            </tr>
          </thead>
          <tbody>
            {intro.current.map((item) => {
              return (
                <Row>
                  <td>
                    {item.dueDate +
                      "(" +
                      dayjs(dayjs()).diff(item.startDt, "year") +
                      ")"}
                  </td>
                  <td>{item.companyNm}</td>
                  <td>{item.team}</td>
                  <td>{item.companyDetail}</td>
                  <td>{item?.work}</td>
                </Row>
              );
            })}
          </tbody>
        </table>
      </Layout>
      <WorkDetailArea>
        <TagList>
          TOP LANG
          {initialState.slice(0, 4).map((item) => (
            <LangButton
              elemType={"BUTTON"}
              isActive={langFilter.some((t) => t === item)}
              type={item}
              onClick={() => handleClickFilter(item)}
            />
          ))}
        </TagList>
        <WorkListArea>
          {workDetail.current.map((item, index) => {
            return (
              <>
                {item.data.map((work) => {
                  return (
                    <div className="data">
                      <li className="title"> {work.title}</li>
                      <li>
                        <div className="label"></div>
                        <div className="value">{work.date}</div>
                      </li>
                      <li>
                        <div className="label"> </div>
                        <div className="value">{work.impTxt}</div>
                      </li>
                      <li>
                        <div className="label">    </div>
                        <div className="value stack">
                          {work.stack.map((item) => {
                            console.log(item);
                            return (
                              <LangButton
                                isActive={langFilter.some((t) => t === item)}
                                elemType={"text"}
                                type={item}
                              />
                            );
                          })}
                        </div>
                      </li>
                      <li>
                        <div className="label">/ </div>
                        <div className="value">{work.result}</div>
                      </li>
                      <li>
                        <div className="label"> </div>
                        <div className="value">
                          {work.detailWork.map((detail) => {
                            return <li>{detail}</li>;
                          })}
                        </div>
                      </li>
                    </div>
                  );
                })}
              </>
            );
          })}
        </WorkListArea>
      </WorkDetailArea>
    </FlexTable>
  );
};

const WorkListArea = styled.div`
  display: flex;
  flex-direction: column;
  flex: 1;
`;

const TagList = styled.div`
  display: flex;
  flex-direction: column;
  position: fixed;
  bottom: 0;
  right: 0;
  gap: 10px;
  margin: 40px;
  background-color: white;
  padding: 9px;
  border-radius: 4px;
  font-size: 5pt;
  font-weight: 600;
  border: 1px solid black;
`;

const WorkDetailArea = styled.div`
  width: 100%;
  margin: 0 auto;
  display: flex;
  gap: 20px;
  padding-right: 3px;
  position: relative;
  justify-content: space-between;

  .data {
    background-color: #ffffff94;
    display: flex;
    flex-direction: column;
    gap: 24px;
    padding: 21px 35px;
    font-size: 1rem;
    border-bottom: 1px solid #ffffff54;

    li {
      display: flex;
      align-items: center;
      &:hover {
        .value {
          font-weight: 400;
          /* color: white; */
        }
      }
      div {
        padding: 7px;
      }
      .label {
        flex: 1;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        max-width: 15rem;
        font-weight: 500;
        background-color: #ffffff94;
        border-bottom: 1px solid white;
      }
      .value {
        flex: 3;
        padding: 0px 20px 0px 13px;

        display: flex;
        flex-direction: column;
        gap: 10px;
      }
      .stack {
        flex-direction: row;
        gap: 3px;
      }
    }

    .title {
      list-style: none;
      font-size: 1.2rem;
      font-weight: 600;
    }
  }

  ::-webkit-scrollbar {
    width: 5.2px;
  }
  ::-webkit-scrollbar-thumb {
    background-color: #dddddd;
    border-radius: 2px;
  }
  ::-webkit-scrollbar-button {
    display: none;
  } /*      */
  ::-webkit-scrollbar-track {
    background-color: transparent;
    min-width: 20px;
  } /*     */
`;

const fadeIn = keyframes`
  from { opacity: 0; }
  to { opacity: 1; z-index: 9999}
`;

const fadeOut = keyframes`
  from { opacity: 1; }
  to { opacity: 0; z-index: -1; pointer-events: none;}
`;

const slideUp = keyframes`
  from {
    // transform: translateY(12.5rem);
    opacity: 0; height : 0px; }
  to {
    // transform: translateY(0);
   opacity: 1; z-index: 9999; height : 100% }
`;

const slideDown = keyframes`
  from { transform: translateY(0); }
  to { transform: translateY(12.5rem); }
`;

const Layout = styled.div`
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1rem;
  user-select: none;
  position: sticky;
  top: 0;

  font-family: "NanumSquare";
  z-index: 1;
  table {
    background: white;
    width: 100%;
    border-collapse: collapse;
    text-align: center;

    thead {
      box-shadow: 1px 1px 7px 0px #b0b0b02b;
    }

    th {
      padding: 5px;
    }
    tr td {
      user-select: none;
      white-space: pre;
      padding: 10px 13px;
      border: 5px solid transparent;
      border-spacing: 25px;
    }
  }
`;

export const FlexTable = styled.div`
  width: ${({ width }) => width};
  font-size: 2rem;
  padding: 10px 1rem;
  /* height: 50rem; */
  font-size: 1rem;
  gap: 10px;
  display: flex;
  flex-direction: column;

  position: sticky;
  top: 0;
  ${({ firstRendering }) =>
    firstRendering
      ? css`
          opacity: 0;
          z-index: -1;
          pointer-events: none;
        `
      : css`
          animation-duration: 0.8s;
          animation-timing-function: ease-out;
          animation-name: ${slideUp};
          animation-fill-mode: forwards;

          ${(props) =>
            props.disappear &&
            css`
              animation-name: ${fadeOut};
            `}
        `};
`;

const Row = styled.tr``;

export default React.memo(EduPage);
